<template>
  <v-container class="container-">
    <div class="feedback-page">

      <v-alert color="#CEE6FF" class="custom-alert">
        <h3 :style="{ color: 'black' }">Evaluating aspects:</h3>
        <v-text> <strong :style="{ color: 'red', 'margin-right': '10px' }">Quality:</strong> Focus on the level of
          detail,
          clarity,
          and fidelity of an image, such as sharpness, color accuracy, and noise levels.</v-text>
        <br>
        <v-text><strong :style="{ color: 'red', 'margin-right': '10px' }">Alignment:</strong> Focus on how closely the
          generated
          image matches the reference text or/and image, such as style, content, or thematic elements.</v-text>
        <br>
        <v-text><strong :style="{ color: 'red', 'margin-right': '10px' }">Aesthetic:</strong> Focus the visual appeal,
          composition, and artistic merit.</v-text>

      </v-alert>

      <v-alert color="#CEE6FF" class="custom-alert">
        <h3 :style="{ color: 'black' }">Reference type(s):</h3>

        <v-text> Each image is guided by provided reference content, such as reference text or/and reference image. If
          both are present on the following pages, the images are guided by both; otherwise, they are guided by one of
          them. </v-text>

        <br>
        <v-text> <strong :style="{ color: 'red', 'margin-right': '10px' }">Note:</strong> You can click on these images
          to zoom it or zoom the page by holding CTRL and scrolling the mouse wheel for better observation.</v-text>

      </v-alert>

      <v-alert color="#CEE6FF" class="custom-alert">
        <h3 :style="{ color: 'black' }">Exampes:</h3>

        <v-text> In the following two pages, we provide two examples to help evaluators understand what is good and what
