        <br>
        <v-text> <strong :style="{ color: 'red', 'margin-right': '10px' }">Note:</strong> "The evaluator can refer to
          the two examples we provided, but the final score is up to you."</v-text>


      </v-alert>

      <v-alert color="#CEE6FF" class="custom-alert">
        <h3 :style="{ color: 'black' }">Finish this Evaluation:</h3>

