<script>


export default {
  name: 'Home',
  data() {
    return {
      // email: 'ryliu68@outlook.com',
      email: 'xxx@xxx',
      valid: false,
      user_form: {
        nickname: '',
        age: '',
        gender: '',
      },

      Genders: ['Male', 'Female', 'Other'],
      submitted: false,
      rules: {
        required: value => !!value || 'This field is required',
        age: value => (value && value > 0) || 'Please enter a valid age'
      }
    }
  },

  computed: {

  },

  methods: {
    handleSubmit() {
      if (this.$refs.user_form.validate()) {
        // 处理表单提交
        console.log('提交的表单数据：', this.form);
        // 这里可以添加发送表单数据到服务器的逻辑
        this.submitted = true;
        // 清空表单

        this.user_form.nickname = null;
        this.user_form.age = null;
        this.user_form.gender = '';
        this.$refs.user_form.resetValidation();
      }
    },

    next_example() {
      this.$router.push({
        path: '/ExampleView',
        query: {
          // num_eval: this.num_eval,
          // example_paths: this.example_paths,
          // json_path: this.json_path,
          user_info: JSON.stringify(this.user_form)
        },
      })
    },


  }
}
</script>

