<template>
  <v-container class="d-flex justify-center align-center container-" fluid>
    <!-- 注意这个I从1开始 -->


    <v-stepper v-model="step" width="1300px">

      <v-stepper-items v-for="i in steps" :key="i">
        <v-stepper-content :step="i">

          <h1 :style="{ 'margin-buttom': '10px', 'margin-top': '0px' }">Reference case: {{ i }}</h1>
 

          <v-row :style="{ 'margin-left': '6px' }">
            <v-col v-for="item in reference_types" :key="item.id">
              <v-card elevation="0" height="300px" justify="center" style="position: relative">

                <v-row justify="center" class="fixed-width-row">
                  <v-col>
                    <h3 :style="{ color: 'red' }">Reference content</h3>
                  </v-col>
                </v-row>
                <div class="custom-hr" :style="{ 'margin-bottom': '6px' }"></div>

                <div style="bottom: 0;position: absolute">
                  <v-row justify="left" class="fixed-width-row" v-if="item.show1" :id="item.id" dense>
                    <v-col :cols="4" :style="{ 'margin-top': '2px' }">
                      Reference text
                    </v-col>
                    <v-col :cols="6.6">
                      <v-text :style="{ color: 'blue' }">{{
                        captions[i - 1]["caption"]
                      }}</v-text>

                    </v-col>
                  </v-row>
                  <v-row justify="left" :style="{ 'margin-top': '2px' }" v-if="item.show2" :id="item.id" dense>
                    <v-col :cols="4.5" :style="{ 'margin-top': '2px' }">
                      Reference image
                    </v-col>

                    <v-col :cols="6.6">
                      <v-img :src="ori_path + '/' + org_filenames[i - 1]['name']" width="150px" height="150px"
                        @click="show(ori_path + '/' + org_filenames[i - 1]['name'])"></v-img>
                    </v-col>
                  </v-row>
                </div>
              </v-card>

              <v-card elevation="0" style="margin-bottom: 5px">
                <div class="custom-hr" :style="{ 'margin-bottom': '6px' }"></div>
                <h3 :style="{ color: 'red' }">AIGC images</h3>
                <div class="custom-hr-re"></div>

                <v-row align="center"  class="fixed-width-row" style="margin-top: 5px">
                  <v-col :col="2">
                  </v-col>
                  <v-col :col="4">
                    <v-img justify="center" class="pa-0" :style="{ 'margin-left': '10px' }" :src="aigc_paths[0] + '/' + aigc_filenames[i - 1][item.id]['name']"
                      width="150px" height="150px"
                      @click="show(aigc_paths[0] + '/' + aigc_filenames[i - 1][item.id]['name'])"></v-img>
                  </v-col>

                  <v-col :col="4">
                    <v-img justify="center" class="pa-0 " :src="aigc_paths[1] + '/' + aigc_filenames[i - 1][item.id]['name']"
                      width="150px" height="150px"
                      @click="show(aigc_paths[1] + '/' + aigc_filenames[i - 1][item.id]['name'])"></v-img>
                  </v-col>

                </v-row>


                <v-row class="fixed-width-row" dense>
                  <v-col :col="2" >Quality</v-col>
                  <v-col :col="4" class="d-flex justify-center align-center"  :style="{'margin-left': '-100px',
                    color: getTextColor(aigc_filenames[i -
                      1][item.id]['Quality'][0])
                  }">{{ aigc_filenames[i -
                      1][item.id]['Quality'][0] }}</v-col>
                  <v-col :col="4" class="d-flex justify-center align-center"  :style="{
                    color: getTextColor(aigc_filenames[i -
                      1][item.id]['Quality'][1])
                  }">{{ aigc_filenames[i -
                      1][item.id]['Quality'][1] }}</v-col>
                </v-row>

                <v-row class="fixed-width-row" dense>
                  <v-col :col="2" >Alignment</v-col>
                  <v-col :col="4" class="d-flex justify-center align-center"
                    :style="{'margin-left': '-100px', color: getTextColor(aigc_filenames[i - 1][item.id]['Alignment'][0]) }">{{
                      aigc_filenames[i - 1][item.id]['Alignment'][0] }}</v-col>
                  <v-col :col="4" class="d-flex justify-center align-center"
                    :style="{ color: getTextColor(aigc_filenames[i - 1][item.id]['Alignment'][1]) }">{{ aigc_filenames[i
                      - 1][item.id]['Alignment'][1] }}</v-col>
                </v-row>

                <v-row class="fixed-width-row" dense>
                  <v-col >Aesthetic</v-col>
                  <v-col class="d-flex justify-center align-center"
                    :style="{'margin-left': '-100px', color: getTextColor(aigc_filenames[i - 1][item.id]['Aesthetic'][0]) }">{{ aigc_filenames[i
                      - 1][item.id]['Aesthetic'][0] }}</v-col>
                  <v-col class="d-flex justify-center align-center"
                    :style="{ color: getTextColor(aigc_filenames[i - 1][item.id]['Aesthetic'][1]) }">{{ aigc_filenames[i
                      - 1][item.id]['Aesthetic'][1] }}</v-col>
                </v-row>

              </v-card>
              <div class="custom-hr-re"></div>
            </v-col>

          </v-row>


          <v-row style="margin: 20px">

            <v-btn style="margin-left: 30px" color="primary" @click="step++" :disabled="step == steps.length"
              v-show="step != steps.length">
              Continue
            </v-btn>

            <v-btn style="margin-left: 30px" color="primary" @click="step--" :disabled="step <= 1">
              BACK
            </v-btn>

            <v-btn style="margin-left: 30px" type="submit" color="primary" @click="next_jude" class="next"
              v-show="step == steps.length">NEXT</v-btn>

          </v-row>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>

  </v-container>
</template>

<script>



export default {
  name: "ExampleView",
  data: () => ({
    example_paths: '',
    json_path: '',
    num_eval: '',
    user_info: '',
    json_data: '',
    results_json: '',
    n: '',
    page_value: "",
    all_value: "",
    isDisabled: true,
    allValuesPositive: false,
    checked: 0,
    flag_page: '',
    flag_all: '',

    steps: [1, 2],

    isConditionMet: false,
    image_names: '', // 初始化数组

    reference_style: '',


    step: 1,
    // ****************************************

    scores: [1, 2, 3, 4, 5],
    // model_nums: [1, 2, 3, 4, 5],
    model_nums: [1, 2, 3, 4],
    // reference_types: [0, 1, 2],
    reference_types: [
      { id: 0, show1: true, show2: false },
      { id: 1, show1: false, show2: true },
      { id: 2, show1: true, show2: true }
    ],
    ty: '',
    org_filenames: [],
    aigc_filenames: [],
    // 时间
    elapsedTime: '0',
    timer: null,
    startTime: null,


    ori_path: '',

    aigc_paths: '',

    reference: ''

  }),

  created() {
    this.startTime = Date.now();
    this.timer = setInterval(this.updateElapsedTime, 1000);
  },

  beforeDestroy() {
    clearInterval(this.timer);
  },
  computed: {

  },

  methods: {

    getTextColor(text) {
      switch (text) {
        case 'good':
          return '#7FFF00'; // 绿色
        case 'bad':
          return '#FFD700'; // 红色
        default:
          return '#000000'; // 默认黑色
      }
    },

    show(url) {
      this.$viewerApi({
        images: [url]
      })
    },

    next_jude() {
      this.$router.push({
        path: '/JudgeView',
        query: {
          user_info: JSON.stringify(this.user_info)
        },
      })
    },
  },

  back_home() {
    this.$router.push({
      path: '/',

    })
  },



  async beforeMount() {
    // this.num_eval = this.$route.query.num_eval // 24
    this.user_info = JSON.parse(this.$route.query.user_info)

    let json_data = require('../assets/captions.json')
    console.log(json_data);

    this.json_data = json_data


    // 生成无序数组
    this.captions = this.json_data.captions;

    this.org_filenames = this.json_data.org_filenames

    this.aigc_filenames = this.json_data.aigc_filenames;

    this.ori_path = "../assets/pic/ORG"


    this.aigc_paths = [
      "../assets/pic/AIGC_bad",
      "../assets/pic/AIGC_good"
    ]



    this.image_names = this.org_filenames;
  },
};
</script>

<style scoped>



.v-container {
  padding-left: 0 !important;
  padding-right: 0 !important;
}

.fixed-width-row {
  width: 390px; /* 设置固定宽度 */
  margin: 0 auto; /* 使其居中显示 */
}

.raido-button-col {
  width: 280px;
}

.col-width {
  width: 80px;
  background-color: aqua;
}

.img-row {
  height: 180px;
}

.eval-aspect {
  width: 150px;
}

.no-wrap {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 73px;
  float: left;
}

.table {
  /* list-style-type: none; */
  padding: 0;
}

.column {
  flex: 1;

  text-align: left;
}

/* 为自定义的横线添加样式 */
.custom-hr {
  border-top: 3px solid black;
  /* 变粗并变黑 */
  width: 390px;
  /* 横线的宽度 */
  margin-top: 5px;
  /* 调整横线与v-card的距离 */
}

.custom-hr-re {
  border-top: 2px solid #999999;
  /* 变粗并变黑 */
  width: 390px;
  /* 横线的宽度 */
  margin-top: 5px;
  /* 调整横线与v-card的距离 */
}

.container- {
  display: flex;
  justify-content: center;
  /* 靠中 */
  align-items: center;
  /* 垂直居中 */
}

.container- :first-child {
  margin-top: auto;
}

.container- :last-child {
  margin-bottom: auto;
}

.flex-layout {
  overflow-x: auto;
  white-space: nowrap;
}


.d-flex {
  display: flex;
}

.justify-center {
  justify-content: center;
}

.align-center {
  align-items: center;
}
</style>


html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow-x: auto;
}