<template>
  <v-container>
    <v-form ref="form" v-model="valid" @submit.prevent="handleSubmit">
      <v-card>
        <v-card-title>
          <h1>Feedback</h1>
        </v-card-title>
        <v-card-text>
          <v-text-field v-model="form.name" label="Name" :rules="[rules.required]" required></v-text-field>
          <v-text-field v-model="form.email" label="E-mail" type="email" :rules="[rules.required, rules.email]"
            required></v-text-field>
          <!-- <v-text-field v-model="form.age" label="Age" type="number" :rules="[rules.required, rules.age]"
            required></v-text-field>
          <v-select v-model="form.gender" :items="genders" label="Gender" :rules="[rules.required]" required></v-select> -->
          <v-textarea v-model="form.feedback_interface" label="Feedback 1) Improvement in User Interaction Interface."
            :rules="[rules.required]" required outlined class="custom-textarea"></v-textarea>

          <v-textarea v-model="form.feedback_eval" label="Feedback 2) Improvement in the Evaluation Work."
            :rules="[rules.required]" required outlined class="custom-textarea"></v-textarea>

        </v-card-text>
        <v-card-actions>
          <!-- <v-btn type="submit" :disabled="!valid" color="primary" @click="exportToJson_Feedback">Submit</v-btn> -->
          <v-btn type="submit" :disabled="!valid" color="primary" @click="submitFeedback">Submit</v-btn>
        </v-card-actions>
      </v-card>
      
      <!-- <v-snackbar v-model="submitted" timeout="3000" >
        Thanks for your feedback!
      </v-snackbar> -->
    </v-form>
  </v-container>
</template>


<script>
import axios from 'axios';

export default {
  data() {
    return {
      num_eval: '',
      elapsedTime:'',
      valid: false,

      form: {
        name: '',
        email: '',
        age: null,
        gender: '',
        feedback_interface: '',
        feedback_eval: ''
      },
      genders: ['Male', 'Female', 'Other'],
      submitted: false,
      rules: {
        required: value => !!value || 'This field is required',
        email: value => /.+@.+\..+/.test(value) || 'Please enter a valid email address',
        age: value => (value && value > 0) || 'Please enter a valid age'
      },

      user_info: '',
      results_json: '',
      image_names: ''
    };
  },
  methods: {
    handleSubmit() {
      if (this.$refs.form.validate()) {
        // 处理表单提交
        console.log('提交的表单数据：', this.form);
        // 这里可以添加发送表单数据到服务器的逻辑
        this.submitted = true;
        // 清空表单
        this.form.name = '';
        this.form.email = '';
        this.form.age = null;
        this.form.gender = '';
        this.form.feedback_interface = '';
        this.form.feedback_eval = '';
        this.$refs.form.resetValidation();
      }
    },

    submitFeedback() {
      let data = {
        user_info: this.user_info,
        
        image_names: this.image_names,
        feedback: {
          name: this.form.name,
          email: this.form.email,
          feedback_interface: this.form.feedback_interface,
          feedback_eval: this.form.feedback_eval
        },
        json: this.results_json,
        elapsedTime:this.elapsedTime

      };

      // 将年龄数据转换为JSON字符串
      const jsonString = JSON.stringify(data);
      // 创建Blob对象
      const blob = new Blob([jsonString], { type: 'application/json' });

      // // 生成基于时间戳的文件名
      // const timestamp = new Date().getTime();
      // const filename = `feedback_${timestamp}.json`;

      // 生成基于当前时间的文件名，精确到秒
      const date = new Date();
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, '0');
      const day = String(date.getDate()).padStart(2, '0');
      const hours = String(date.getHours()).padStart(2, '0');
      const minutes = String(date.getMinutes()).padStart(2, '0');
      const seconds = String(date.getSeconds()).padStart(2, '0');
      const filename = `feedback_${this.user_info.nickname}_${year}-${month}-${day}_${hours}-${minutes}-${seconds}.json`;

      // 创建FormData对象
      const formData = new FormData();
      formData.append('file', blob, filename);

      // 上传文件到服务器
      // localhost or 192.168.1.106 or 8.138.121.232
      axios
        .post('http://8.138.121.232:3000/upload', formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        })
        .then(response => {
          console.log('文件上传成功:', response.data);
          alert('Feedback submitted successfully!');
        })
        .catch(error => {
          console.error('文件上传失败:', error);
          alert('Feedback submission failed!');
        });
    },
  },

  async beforeMount() {
    this.user_info = JSON.parse(this.$route.query.user_info),
    this.num_eval= this.$route.query.num_eval,
    this.elapsedTime= this.$route.query.elapsedTime,

    // this.image_names = new Array(this.num_eval)
    this.image_names = this.$route.query.image_names,  

    this.results_json = JSON.parse(this.$route.query.json)

    console.log(this.user_info)
    console.log(this.num_eval)
    console.log(this.image_names)
    console.log(this.results_json)
  }
};
</script>

<style scoped>
.v-card {
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
}

.custom-textarea {
  border: 1px solid #ddd;
  border-radius: 4px;
}
</style>
