<template>
  <v-container class="d-flex justify-center align-center" fluid>
    <!-- 注意这个I从1开始 -->
    <v-stepper width="1400px" hight="1200px" v-model="step">

      <v-stepper-items v-for="i in captions.length" :key="i">
        <v-stepper-content :step="i">
          <v-row>
            <v-col cols="4" v-for="item in reference_types" :key="item.id">
              <v-card elevation="0" height="260px" justify="center" style="position: relative">

                <v-row justify="center">
                  <v-col>
                    <h3 :style="{ color: 'red' }">Reference content</h3>
                  </v-col>
                </v-row>
                <div class="custom-hr" :style="{ 'margin-bottom': '6px' }"></div>

                <div style="bottom: 0;position: absolute">
                  <v-row justify="left" v-if="item.show1" :id="item.id" dense>
                    <v-col :cols="4" :style="{ 'margin-top': '2px' }">
                      Reference text
                    </v-col>
                    <v-col :cols="6.6" class="d-flex justify-center align-center">
                      <v-text :style="{ color: 'blue' }">{{
                        captions[i - 1]["caption"]
                      }}</v-text>

                    </v-col>
                  </v-row>
                  <v-row justify="left" :style="{ 'margin-top': '2px' }" v-if="item.show2" :id="item.id" dense>
                    <v-col :cols="4.5" :style="{ 'margin-top': '2px' }">
                      Reference image
                    </v-col>

                    <v-col :cols="6.6">
                      <v-img :src="ori_path + '/' + org_filenames[i - 1]['name']" width="150px" height="150px"
                        @click="show(ori_path + '/' + org_filenames[i - 1]['name'])"></v-img>
                    </v-col>
                  </v-row>
                </div>
              </v-card>

              <v-card elevation="0" hight="1200px" style="margin-bottom: 10px">
                <div class="custom-hr" :style="{ 'margin-bottom': '6px' }"></div>
                <h3 :style="{ color: 'red' }">AIGC images</h3>
                <div class="custom-hr-re"></div>
                <v-text name="vt_table">
                  <v-row class="img-row"   v-for="m in model_nums" :key="m">
                    <v-col :cols="4.4" justify="left">
                      <v-text>model {{ m }}</v-text>
                      <v-img :src="aigc_paths[m - 1] + '/' + aigc_filenames[i - 1][item.id]" width="150px"
                        height="150px" @click="show(aigc_paths[m - 1] + '/' + aigc_filenames[i - 1][item.id])"></v-img>
                    </v-col>
                    <v-col :cols="5.9" justify="left" class="pa-0,ma-0">
                      <div :style="{ 'margin-top': '30px' }">
                      </div>

                      <v-col class="raido-button-col">

                        <div class='no-wrap'>
                          <v-text>Quality</v-text>
                        </div>

                        <v-text :style="{ height: '30px', 'margin-left': '6px' }" v-for="n in scores" :key="n">
                          <label :style="{ 'margin-right': '3px' }" :for="i + item.id + '_' + m + '_' + n">{{ n
                            }}</label>
                          <input type="radio" :id="i + item.id + '_' + m + '_' + n" :value="n"
                            :name="'qs' + '_' + i + '_' + item.id + '_' + m"
                            v-model="results_json[i - 1][item.id][m - 1][0]" />
                        </v-text>
                      </v-col>

                      <v-col class="raido-button-col">
                        <div class='no-wrap'>
                          <v-text>Alignment</v-text>
                        </div>

                        <v-text :style="{ height: '30px', 'margin-left': '6px' }" v-for="n in scores" :key="n">
                          <label :style="{ 'margin-right': '3px' }" :for="i + item.id + '_' + m + '_' + n">{{ n
                            }}</label>
                          <input type="radio" :id="i + item.id + '_' + m + '_' + n"
                            :name="'cs' + '_' + i + '_' + item.id + '_' + m" :value="n"
                            v-model="results_json[i - 1][item.id][m - 1][1]" />
                        </v-text>
                      </v-col>
                      <v-col class="raido-button-col pd-0">
                        <div class='no-wrap '>
                          <v-text>Aesthetic</v-text>
                        </div>

                        <v-text :style="{ height: '30px', 'margin-left': '6px' }" v-for="n in scores" :key="n">
                          <label :style="{ 'margin-right': '3px' }" :for="i + item.id + '_' + m + '_' + n">{{ n
                            }}</label>
                          <input type="radio" :id="i + item.id + '_' + m + '_' + n"
                            :name="'as' + '_' + i + '_' + item.id + '_' + m" :value="n"
                            v-model="results_json[i - 1][item.id][m - 1][2]" />
                        </v-text>
                      </v-col>
                      <!-- <h5>{{ i }}{{ results_json[i - 1][item.id][m - 1] }}</h5> -->
                    </v-col>

                  </v-row>
                </v-text>
                <br>

              </v-card>
              <div class="custom-hr-re"></div>
            </v-col>
          </v-row>

          <v-card elevation="0" width="550px" style="margin-top: 10px; margin-bottom:10px">
            <v-text name="progress">Evaluate progress: {{ i }}/{{ num_eval }}</v-text>
            <v-text></v-text>

            <v-text style="margin-left: 30px; margin-bottom:80px" name="time_used">Time elapsed: {{
              elapsedTime
            }}s
            </v-text>

            <v-text style="margin-left: 30px;" name="time">Estimated time: {{ 120 * num_eval }}s</v-text>
          </v-card>

          <v-btn color="primary" @click="step++" :disabled="!pageSelected">
            Continue
          </v-btn>

          <v-btn style="margin-left: 30px" color="primary" @click="step--" :disabled="step <= 1">
            BACK
          </v-btn>

          <v-btn style="margin-left: 30px" color="primary" v-show="step == captions.length" :disabled="!allSelected"
            @click="submitForm">
            FINISH
          </v-btn>

          <v-btn style="margin-left: 30px" color="primary" v-show="step == captions.length" :disabled="!allSelected"
            @click="feedback">FEEDBACK
          </v-btn>

          <!-- <v-row style="margin: 20px">
            <v-btn color="primary" @click="step++">
              Continue
            </v-btn>

            <v-btn style="margin-left: 30px" color="primary" @click="step--" :disabled="step <= 1">
              BACK
            </v-btn>

            <v-btn style="margin-left: 30px" color="primary" v-show="step == captions.length" @click="submitForm">
              FINISH
            </v-btn>

            <v-btn style="margin-left: 30px" color="primary" v-show="step == captions.length" @click="feedback">FEEDBACK
            </v-btn>
          </v-row>  -->

        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>
  </v-container>
</template>

<script>


// import { load_images, shuffle, load_json } from "@/plugins/file_reader";
// import { saveAs } from 'file-saver';
import FileSaver from 'file-saver'
import axios from 'axios';
// import 'vuetify/dist/vuetify.min.css';


export default {
  name: "JudgeView",
  data: () => ({
    example_paths: '',
    json_path: '',
    num_eval: 10,
    user_info: '',
    json_data: '',
    results_json: '',
    n: '',
    page_value: "",
    all_value: "",
    isDisabled: true,
    allValuesPositive: false,
    checked: 0,
    flag_page: '',
    flag_all: '',

    isConditionMet: false,
    image_names: '', // 初始化数组

    reference_style: '',


    step: 1,
    // ****************************************

    scores: [1, 2, 3, 4, 5],
    // model_nums: [1, 2, 3, 4, 5],
    model_nums: [1, 2, 3, 4],
    // reference_types: [0, 1, 2],
    reference_types: [
      { id: 0, show1: true, show2: false },
      { id: 1, show1: false, show2: true },
      { id: 2, show1: true, show2: true }
    ],
    ty: '',
    org_filenames: [],
    aigc_filenames: [],
    // 时间
    elapsedTime: '0',
    timer: null,
    startTime: null,


    ori_path: '',

    aigc_paths: '',

    reference: ''

  }),

  created() {
    this.startTime = Date.now();
    this.timer = setInterval(this.updateElapsedTime, 1000);
  },

  beforeDestroy() {
    clearInterval(this.timer);
  },
  computed: {
    pageSelected() {
      this.page_value = this.results_json[this.step - 1]

      this.flag_page = true
      for (let i = 0; i < this.page_value.length; i++) {
        for (let j = 0; j < this.page_value[i].length; j++) {
          for (let k = 0; k < this.page_value[i][j].length; k++) {
            if (this.page_value[i][j][k] < 0) {
              this.flag_page = false
            }
          }
        }
      }

      if (this.step == this.captions.length) {
        this.flag_page = false
      }

      return this.flag_page
    },

    allSelected() {
      this.all_value = this.results_json

      this.flag_all = true
      for (let i = 0; i < this.all_value.length; i++) {
        for (let j = 0; j < this.all_value[i].length; j++) {
          for (let k = 0; k < this.all_value[i][j].length; k++) {
            for (let l = 0; l < this.all_value[i][j][k].length; l++) {
              if (this.all_value[i][j][k][l] < 0) {
                this.flag_all = false
              }
            }
          }
        }
      }

      return this.flag_all
    },
  },

  methods: {
    updateElapsedTime() {
      const currentTime = Date.now();
      const elapsed = Math.floor((currentTime - this.startTime) / 1000);
      this.elapsedTime = `${elapsed}`;
    },

    show(url) {
      this.$viewerApi({
        images: [url]
      })
    },

    feedback() {
      this.$router.push({
        path: '/FeedbackView',
        query: {
          num_eval: this.num_eval,
          elapsedTime:this.elapsedTime,
          user_info: JSON.stringify(this.user_info),
          json: JSON.stringify(this.results_json),
          image_names: this.image_names
        },
      })
    },

    submitForm() {
      let data = {
        user_info: this.user_info,
        image_names: this.image_names,
        json: this.results_json,
        elapsedTime:this.elapsedTime
      };

      // 将年龄数据转换为JSON字符串
      const jsonString = JSON.stringify(data);
      // 创建Blob对象
      const blob = new Blob([jsonString], { type: 'application/json' });

      // // 生成基于时间戳的文件名
      // const timestamp = new Date().getTime();
      // const filename = `eval_${timestamp}.json`;

      // 生成基于当前时间的文件名，精确到秒
      const date = new Date();
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, '0');
      const day = String(date.getDate()).padStart(2, '0');
      const hours = String(date.getHours()).padStart(2, '0');
      const minutes = String(date.getMinutes()).padStart(2, '0');
      const seconds = String(date.getSeconds()).padStart(2, '0');
      const filename = `eval_${this.user_info.nickname}_${year}-${month}-${day}_${hours}-${minutes}-${seconds}.json`;

      // 创建FormData对象
      const formData = new FormData();
      formData.append('file', blob, filename);

      // 上传文件到服务器
      // localhost or 192.168.1.106 or 8.138.121.232
      axios
        .post('http://8.138.121.232:3000/upload', formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        })
        .then(response => {
          console.log('文件上传成功:', response.data);
          alert('Data submitted successfully. You can continue to click on the "FEEDBACK" to give us feedback. ');
        })
        .catch(error => {
          console.error('文件上传失败:', error);
          alert('Data submission failed!');
        });
    },
  },

  async beforeMount() {
    // this.num_eval = this.$route.query.num_eval // 24
    this.user_info = JSON.parse(this.$route.query.user_info)

    let json_data = require('../assets/human_5_vs_id_selected.json')
    console.log(json_data);

    this.json_data = json_data

    // todo 随机排序json_data  start 
    //
    function generateUnorderedArray(N) {
      let array = [];
      for (let i = 0; i < N; i++) {
        array.push(i);
      }
      // 使用 Fisher-Yates 算法来进行无序排列
      for (let i = array.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
      }
      return array;
    }

    // 生成无序数组
    const unorderedArray = generateUnorderedArray(this.num_eval);
    console.log(unorderedArray); // [4, 3, 2, 0, 1],[4, 1, 2, 0, 3],[3, 2, 0, 1, 4]

    // 
    function getValuesByIndices(arr, indices) {
      return indices.map(index => arr[index]);
    }

    // 生成无序数组
    this.captions = getValuesByIndices(this.json_data.captions, unorderedArray);
    // console.log(this.captions);

    this.org_filenames = getValuesByIndices(this.json_data.org_filenames, unorderedArray);
    // console.log(this.org_filenames);

    this.aigc_filenames = getValuesByIndices(this.json_data.aigc_filenames, unorderedArray);
    // console.log(this.aigc_filenames);

    this.ori_path = "../assets/human_test_5/v2/ORG"


    this.aigc_paths = [
      // "../assets/human_test_5/v2/SD_V14",
      // "../assets/human_test_5/v2/SD_V15",
      "../assets/human_test_5/v2/SD_V21",
      "../assets/human_test_5/v2/SD_XL",
      "../assets/human_test_5/v2/Versatile",
      "../assets/human_test_5/v2/Dalle2"
    ]


    // 动态创建一个四维数组
    function generate4DArray(dim1, dim2, dim3, dim4) {
      const array = new Array(dim1);
      for (let i = 0; i < dim1; i++) {
        array[i] = new Array(dim2);
        for (let j = 0; j < dim2; j++) {
          array[i][j] = new Array(dim3);
          for (let k = 0; k < dim3; k++) {
            array[i][j][k] = new Array(dim4);
            for (let l = 0; l < dim4; l++) {
              array[i][j][k][l] = -1; // 初始化值为0，可以根据需要进行修改
            }
          }
        }
      }
      return array;
    }

    this.results_json = generate4DArray(this.num_eval, 3, this.model_nums.length, 3)

    this.image_names = this.org_filenames;
  },
};
</script>

<style scoped>
.raido-button-col {
  width: 260px;
  padding-left: 0px;
  padding-right: 0px;
}

.img-row {
  height: 180px;
}

.eval-aspect {
  width: 150px;
}

.no-wrap {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 73px;
  float: left;
}

.table {
  /* list-style-type: none; */
  padding: 0;
}

.column {
  flex: 1;

  text-align: left;
}

/* 为自定义的横线添加样式 */
.custom-hr {
  border-top: 3px solid black;
  /* 变粗并变黑 */
  width: 410px;
  /* 横线的宽度 */
  margin-top: 5px;
  /* 调整横线与v-card的距离 */
}

.custom-hr-re {
  border-top: 2px solid #999999;
  /* 变粗并变黑 */
  width: 410px;
  /* 横线的宽度 */
  margin-top: 5px;
  /* 调整横线与v-card的距离 */
}

.container- {
  display: flex;
  justify-content: center;
  /* 靠中 */
  align-items: center;
  /* 垂直居中 */
}

.container- :first-child {
  margin-top: auto;
}

.container- :last-child {
  margin-bottom: auto;
}

.flex-layout {
  overflow-x: auto;
  white-space: nowrap;
}
</style>
